/* --- START OF FILE Store.css --- */

.store-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    padding-top: 80px; /* Match dashboard for navbar spacing */
    min-height: 100vh;
    box-sizing: border-box;
    width: 100%;
    overflow-x: hidden; /* Prevent horizontal scroll during animations */
}

.store-title {
    color: var(--text-primary);
    margin-bottom: 30px;
    font-size: 1.8em;
    font-weight: 600;
    text-align: center;
    width: 100%;
    max-width: 1100px; /* Align with dashboard grid max-width */
    /* Optional: Add subtle entrance */
    /* animation: fadeInDown 0.5s ease-out forwards; */
}

.store-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 25px;
    max-width: 1100px;
    width: 100%;
    margin-top: 20px;
}

/* --- Product Card Styling --- */
.product-card {
    background-color: var(--bg-secondary);
    border-radius: 8px;
    box-shadow: var(--card-shadow);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border: 1px solid var(--bg-accent);
    position: relative;
    /* Use opacity and transform for smoother transitions */
    transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1),
                box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1),
                border-color 0.3s ease; /* Add border-color transition */
}

/* Entrance Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.98);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.product-card-enter {
  opacity: 0; /* Start hidden */
  animation: fadeInUp 0.5s cubic-bezier(0.25, 0.8, 0.25, 1) forwards;
  /* animation-delay is set inline via style prop in React */
}

.product-card:hover {
    transform: translateY(-5px) scale(1.01); /* Slightly more pronounced lift + scale */
    box-shadow: var(--card-hover-shadow);
    border-color: var(--accent-yellow); /* Highlight border on hover */
}

.product-image-container {
    height: 180px;
    background-color: var(--bg-tertiary);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* Keep image zoom contained */
}

.product-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.4s ease; /* Slightly longer transition */
}

.product-card:hover .product-image {
    transform: scale(1.08); /* Slightly larger zoom */
}

.product-details {
    padding: 20px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.product-name {
    font-size: 1.2em;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 10px;
}

.product-description {
    font-size: 0.9em;
    color: var(--text-secondary);
    line-height: 1.5;
    margin-bottom: 15px;
    flex-grow: 1;
}

.product-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: auto;
    padding-top: 15px;
    border-top: 1px solid var(--bg-tertiary);
}

.product-price {
    font-size: 1.1em;
    font-weight: 700;
    color: var(--accent-green);
}

/* --- Add to Cart Button --- */
.button-add-to-cart {
    background-color: var(--accent-yellow);
    color: #fff;
    box-shadow: 0 2px 5px rgba(251, 145, 47, 0.3);
    padding: 8px 16px;
    font-size: 0.9em;
    display: inline-flex; /* Align icon and text nicely */
    align-items: center;
    justify-content: center;
    gap: 6px; /* Space between icon and text */
    transition: background-color 0.2s ease, transform 0.15s ease, box-shadow 0.2s ease; /* Add transform */
}

.button-add-to-cart:hover:not(:disabled) {
    background-color: #f97e1a;
    box-shadow: 0 4px 8px rgba(251, 145, 47, 0.4);
    transform: translateY(-1px); /* Subtle lift on hover */
}

/* Active state for immediate feedback on click */
.button-add-to-cart:active:not(:disabled) {
    transform: scale(0.97); /* Push down effect */
    box-shadow: 0 1px 3px rgba(251, 145, 47, 0.2);
}

.button-add-to-cart:disabled {
     background-color: var(--bg-tertiary);
     border-color: var(--bg-accent);
     color: var(--text-disabled);
     box-shadow: none;
     opacity: 0.6;
     cursor: not-allowed;
}

/* Spinner animation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.spinner-icon {
  animation: spin 1s linear infinite;
  /* Adjust size/margin if needed */
  /* margin-right: 8px; */
}


/* Badge Example (Optional) */
.product-badge {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: var(--accent-purple);
    color: white;
    padding: 3px 8px;
    border-radius: 4px;
    font-size: 0.75em;
    font-weight: 600;
    z-index: 1;
}

/* --- Accessibility --- */
@media (prefers-reduced-motion: reduce) {
  /* Disable or reduce animations */
  .product-card,
  .product-card-enter,
  .product-image,
  .button-add-to-cart,
  .spinner-icon {
    animation: none !important;
    transition: none !important;
  }
  /* Keep essential feedback, maybe remove transforms */
  .product-card:hover {
    transform: none;
    /* Keep shadow change? Or remove it too? Preference */
    /* box-shadow: var(--card-hover-shadow); */
    border-color: var(--accent-yellow); /* Keep border highlight */
  }
   .button-add-to-cart:hover:not(:disabled) {
     transform: none;
   }
   .button-add-to-cart:active:not(:disabled) {
     transform: none;
   }
}


/* --- Responsive adjustments --- */
/* (Keep existing media queries as they are) */
@media (max-width: 768px) {
    .store-title {
        font-size: 1.6em;
        margin-bottom: 20px;
    }
    .store-grid {
        gap: 20px;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
    .product-image-container {
        height: 160px;
    }
    .product-details {
        padding: 15px;
    }
}

@media (max-width: 480px) {
    /* .store-grid {
        grid-template-columns: 1fr;
    } */
    .product-name {
        font-size: 1.1em;
    }
    .product-price {
        font-size: 1em;
    }
    .button-add-to-cart {
        padding: 6px 12px;
        font-size: 0.85em;
    }
}

