/* src/Login.css (or Login-Dark-Regz.css) */

/* --- CSS Variables for "Regz Cheat" Dark Theming --- */
:root {
  /* Core Palette - Adjusted to match image */
  --primary-color: #3b82f6;         /* Button blue (similar to Tailwind Blue 500) */
  --primary-hover: #2563eb;         /* Darker blue for hover */
  --primary-focus-ring: rgba(59, 130, 246, 0.5); /* Brighter blue glow for focus */

  --gradient-start: #a855f7;       /* Purple/Magenta for gradient (Tailwind Purple 500) */
  --gradient-end: #22d3ee;         /* Cyan/Blue for gradient (Tailwind Cyan 400) */

  --text-color-high: #ffffff;      /* White for main text */
  --text-color-medium: #a0aec0;     /* Lighter gray for subtitle, labels (Tailwind Gray 400) */
  --text-color-low: #718096;       /* Darker gray for placeholders (Tailwind Gray 500) */
  --icon-color: var(--text-color-medium); /* Color for input icons */

  --background-color: #0a0a10;    /* Very dark navy/black background */
  --container-bg: #16191d69;       /* Dark gray/blue for the login box */
  --input-bg: #0d1117;           /* Very dark input background (GitHub dark dim) */
  --input-border: #30363d;       /* Subtle border color (GitHub dark dim border) */
  --input-focus-border: var(--primary-color); /* Use primary blue for focus border */

  /* States & Feedback */
  --error-bg: #4d1818;            /* Dark red background for errors */
  --error-text: #f48a8a;          /* Lighter red text for errors */
  --error-border: rgba(244, 138, 138, 0.3); /* Subtle red border for errors */
  --disabled-bg: #21262d;         /* GitHub dark dim disabled bg */
  --disabled-text: #484f58;       /* GitHub dark dim disabled text */

  /* UI Elements */
  --border-radius-sm: 8px;         /* Input/Button radius from image */
  --border-radius-md: 12px;        /* Container radius from image */
  --gradient-border-height: 3px;   /* Thickness of the top gradient border */

  /* Icon and Input Sizing/Padding */
  --icon-size: 1rem; /* Size for input icons */
  --input-padding-horizontal: 15px;
  --input-padding-vertical: 12px;
  --input-icon-padding-left: calc(var(--input-padding-horizontal) + var(--icon-size) + 10px); /* Space for left icon */
  --input-icon-padding-right: 45px; /* Space for right (password toggle) icon */
}

