/* In App.css or index.css */

.login-page-wrapper {
  display: grid;           /* Enable Grid */
  place-items: center;     /* Centers items on both axes */
  min-height: 100vh;       /* Make wrapper at least full viewport height */
  width: 100%;             /* Make wrapper full width */
  background-color: var(--background-color); /* Optional: Set background */
  padding: 1rem;           /* Optional: Add padding around the card */
  box-sizing: border-box;
}

/* Ensure body/html take up height too, if necessary */
html, body, #root {
    height: 100%;
    margin: 0;
    padding: 0;
}


/* --- Login Container Specific Styles --- */
.login-container {
background-color: var(--container-bg);
padding: calc(var(--gradient-border-height) + 30px) 50px 40px 50px; /* Adjusted top padding */
border-radius: var(--border-radius-md);
max-width: 420px;
width: 90%;
margin: 2rem auto;
text-align: center;
box-sizing: border-box;
position: relative; 
overflow: hidden; 
border: 1px solid #2a2f37; 
}

/* --- Gradient Top Border --- */
.login-container::before {
content: '';
position: absolute;
top: 0;
left: 0;
right: 0;
height: var(--gradient-border-height);
background-image: linear-gradient(to right, var(--gradient-start), var(--gradient-end));
z-index: 1; /* Ensure it's above background but below content if needed */
}

/* --- Login Header --- */
.login-header {
display: flex;
align-items: center;
justify-content: center; /* Center header items */
margin-bottom: 1rem; /* Slightly less space */
gap: 0.75rem;
}

.login-brand-icon {
background-color: #0d1117; /* Dark background matching input fields */
color: white;
width: 36px; /* Slightly smaller */
height: 36px;
border-radius: 50%;
display: inline-flex;
align-items: center;
justify-content: center;
font-size: 1.2rem; /* Adjust if using a real icon */
font-weight: normal; /* Assuming icon is SVG/Font */
flex-shrink: 0;
border: 1px solid var(--input-border); /* Subtle border like inputs */
/* Replace 'W' with your actual icon (e.g., SVG, Font Awesome) */
}

.login-header h2 { /* Style the "Walker Regedits" text */
  margin: 0;
  font-size: 1.25rem; /* Adjust size as needed */
  color: var(--text-color-high);
  font-weight: 600;
}


.login-container h3 {
font-size: 1.75rem; 
margin-top: 1rem; 
margin-bottom: 0.5rem;
font-weight: 600;
color: var(--text-color-high);
}

.login-subtitle {
color: var(--text-color-medium); 
margin-bottom: 2.5rem; 
font-size: 0.9rem; 
}

.error-message {
background-color: var(--error-bg);
color: var(--error-text);
padding: 0.8rem 1rem;
border-radius: var(--border-radius-sm);
margin-bottom: 0; 
font-size: 0.9rem;
text-align: left;
border: 1px solid var(--error-border);
overflow: hidden; /* Needed for height animation in framer-motion */
}

/* --- Form Styling --- */
form {
display: flex;
flex-direction: column;
gap: 1.5rem; /* More space between input groups */
text-align: left;
}

.input-group {
display: flex;
flex-direction: column;
}

.input-group label {
margin-bottom: 0.5rem;
font-size: 0.85rem;
font-weight: 500;
color: var(--text-color-medium); /* Lighter gray label */
}

.input-wrapper {
position: relative; /* Crucial for icon positioning */
}

/* --- Input Icon Styling (Left Side) --- */
.input-icon {
position: absolute;
left: var(--input-padding-horizontal);
top: 50%;
transform: translateY(-50%);
color: var(--icon-color);
font-size: var(--icon-size);
pointer-events: none; /* Don't interfere with input clicks */
z-index: 2; /* Ensure icon is above input background */
}

/* --- Input Fields --- */
input[type="text"],
input[type="password"] {
width: 100%;
/* Adjust padding to make space for the left icon */
padding: var(--input-padding-vertical) var(--input-padding-horizontal) var(--input-padding-vertical) var(--input-icon-padding-left);
border: 1px solid var(--input-border);
border-radius: var(--border-radius-sm);
font-size: 0.95rem;
color: var(--text-color-high);
background-color: var(--input-bg);
transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
box-sizing: border-box;
position: relative; /* Needed if using z-index on icons */
z-index: 1; /* Ensure input background is below icons */
}

/* Specifically adjust password input padding for the right toggle icon */
/* Also handles when the type is toggled to 'text' */
input[type="password"],
input[type="text"][id="password"] {
padding-right: var(--input-icon-padding-right);
}


/* --- Password Toggle Button Styling (Right Side) --- */
.password-toggle-button {
position: absolute;
right: 0; /* Align to the right edge */
top: 0; /* Align to the top */
bottom: 0; /* Stretch vertically */
width: var(--input-icon-padding-right); /* Use the padding value for width */
background: none;
border: none;
padding: 0; /* Remove default padding */
margin: 0; /* Remove default margin */
display: flex;
align-items: center;
justify-content: center;
color: var(--icon-color);
font-size: 1.2rem; /* Slightly larger icon for easier clicking */
cursor: pointer;
transition: color 0.2s ease;
z-index: 2; /* Ensure button is clickable above input */
}

.password-toggle-button:hover:not(:disabled),
.password-toggle-button:focus {
color: var(--text-color-high); /* Brighten icon on hover/focus */
outline: none;
}

.password-toggle-button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

input[type="text"]::placeholder,
input[type="password"]::placeholder {
color: var(--text-color-low); /* Darker gray placeholder */
opacity: 1;
}

input[type="text"]:focus,
input[type="password"]:focus {
outline: none;
border-color: var(--input-focus-border);
box-shadow: 0 0 0 3px var(--primary-focus-ring); /* Blue glow on focus */
}

input:disabled {
background-color: var(--disabled-bg);
border-color: var(--input-border); /* Keep border subtle */
color: var(--disabled-text);
cursor: not-allowed;
}
/* Ensure icons inside disabled wrappers are also dimmed */
/* Using adjacent sibling selector (+) assuming button is immediately after input */
input:disabled + .password-toggle-button {
  opacity: 0.5;
}
/* Using general sibling selector (~) for the left icon as it might not be adjacent */
.input-wrapper input:disabled ~ .input-icon {
  opacity: 0.5;
}


/* --- Login Button --- */
.login-button {
display: inline-flex;
align-items: center;
justify-content: center; /* Center icon and text */
gap: 0.6rem; /* Space between icon and text */
width: 100%; /* Full width button */
padding: var(--input-padding-vertical) 20px; /* Use variable for consistency */
background-color: var(--primary-color);
color: white;
font-size: 0.95rem; /* Match input font size */
font-weight: 500;
border: none;
border-radius: var(--border-radius-sm);
cursor: pointer;
transition: background-color 0.2s ease-in-out, transform 0.1s ease, box-shadow 0.2s ease;
margin-top: 0.5rem; /* Space above button */
overflow: hidden; /* Prevent content shifts during animation */
position: relative; /* If needed for absolute positioning of spinner/icon */
}

.login-button svg { /* Styles for both Arrow and Spinner */
font-size: 1rem; /* Adjust as needed */
transition: transform 0.2s ease;
vertical-align: middle; /* Helps alignment with text */
}

.login-button:hover:not(:disabled) {
background-color: var(--primary-hover);
}

.login-button:focus-visible { /* Modern focus state */
  outline: none;
  box-shadow: 0 0 0 3px var(--primary-focus-ring);
}

/* :active scale handled by framer-motion whileTap */

.login-button:disabled {
background-color: var(--disabled-bg);
color: var(--disabled-text);
cursor: not-allowed;
}

.login-button:disabled svg:not(.spinner) { /* Don't dim the active spinner */
opacity: 0.5;
}

/* --- Spinner Animation --- */
.spinner {
animation: spin 1s linear infinite;
}

@keyframes spin {
0% { transform: rotate(0deg); }
100% { transform: rotate(360deg); }
}


/* --- Media Query --- */
@media (max-width: 480px) {
.login-container {
  padding: calc(var(--gradient-border-height) + 25px) 25px 30px 25px; /* Adjust padding */
  width: 95%;
  border: none; /* Remove subtle border on small screens */
  background-color: var(--container-bg); /* Keep background */
}

/* Adjust icon padding/positioning slightly for smaller screens if needed */
 :root {
   /* Example: Slightly reduce horizontal padding and adjust icon spacing */
   --input-padding-horizontal: 12px;
   --input-icon-padding-left: calc(var(--input-padding-horizontal) + var(--icon-size) + 8px); /* Less gap */
   --input-icon-padding-right: 40px; /* Slightly smaller toggle button area */
 }

 .password-toggle-button {
   width: var(--input-icon-padding-right);
 }

 .login-container h3 {
     font-size: 1.6rem; /* Slightly smaller title on mobile */
 }

 .login-subtitle {
      margin-bottom: 2rem; /* Less space before form on mobile */
 }
}

/* --- END OF FILE Login.css --- */