/* --- START OF FILE Navbar.css --- */

/* ... (keep all your existing root variables and desktop styles) ... */
:root {
    /* ... existing variables ... */
    --accent-color: #0099ff;
    --navbar-bg: #1a1d3a;
    --navbar-blur: 10px;
    --text-primary: #e8eaf6;
    --text-secondary: #ffffff;
    --hover-bg: rgba(255, 255, 255, 0.1);
    --active-bg: rgba(0, 153, 255, 0.15);
    --dropdown-bg: #2a2d4e;
    --bg-accent: rgba(255, 255, 255, 0.1);
    --bg-tertiary: rgba(255, 255, 255, 0.05);
    --accent-red: #f5365c;
    --border-color: rgba(255, 255, 255, 0.15);
    --mobile-breakpoint: 768px;
    --mobile-nav-bg: #202344;
    --timing-fast: 0.2s;
    --timing-medium: 0.3s;
    --timing-slow: 0.6s;
    --easing-smooth: cubic-bezier(0.25, 0.8, 0.25, 1);
    --easing-slide: cubic-bezier(0.23, 1, 0.32, 1);
}

/* --- DESKTOP STYLES --- */
.navbar {
    /* ... existing styles ... */
    position: fixed;
    top: 1rem;
    left: 50%;
    text-decoration: none;
    width: 90%;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.6rem 1.5rem;
    background-color: rgba(26, 29, 58, 0.8);
    backdrop-filter: blur(var(--navbar-blur));
    -webkit-backdrop-filter: blur(var(--navbar-blur));
    color: var(--text-primary);
    z-index: 1000;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    border: 1px solid var(--border-color);
    transform: translateX(-50%) translateY(-150%);
    opacity: 0;
    transition: transform var(--timing-slow) var(--easing-slide),
                opacity var(--timing-medium) ease-out,
                background-color var(--timing-medium) var(--easing-smooth);
}

.navbar-visible {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
}

.navbar-left,
.navbar-right {
    display: flex;
    align-items: center;
    gap: 1rem;
}
.navbar-left { flex-grow: 1; }
.navbar-right { flex-shrink: 0; }

.navbar-brand {
    /* ... existing styles ... */
    display: flex;
    align-items: center;
    gap: 0.6rem;
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--text-secondary);
    text-decoration: none;
    transition: color var(--timing-medium) var(--easing-smooth), transform var(--timing-medium) var(--easing-smooth);
    margin-right: 1.5rem;
    flex-shrink: 0;
}
.navbar-brand:hover {
    color: var(--accent-color);
    text-decoration: none;
    transform: scale(1.03);
}
.navbar-logo-img { height: 28px; width: auto; }

/* --- Desktop Links Container --- */
.navbar-links {
    display: flex;
    gap: 0.4rem;
    /* Reset mobile styles */
    position: static;
    flex-direction: row;
    background-color: transparent;
    padding: 0;
    box-shadow: none;
    border: none;
    max-height: none;
    opacity: 1;
    visibility: visible;
    transform: none;
    overflow: visible;
    pointer-events: auto;
    width: auto;
}
/* Hide mobile-specific user menu items on desktop */
.navbar-link-mobile-usermenu {
    display: none;
}


.navbar-link {
    /* ... existing styles ... */
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    color: var(--text-primary);
    text-decoration: none;
    border-radius: 6px;
    position: relative;
    overflow: visible;
    transition: background-color var(--timing-fast) var(--easing-smooth),
                color var(--timing-fast) var(--easing-smooth);
    font-size: 0.85rem;
    font-weight: 500;
    white-space: nowrap;
    width: auto;
    border-bottom: none;
    justify-content: center;
}
/* ... existing ::before, :hover, :focus, .active styles ... */
.navbar-link::before { content: ''; position: absolute; bottom: 0; left: 0; width: 100%; height: 2px; background-color: var(--accent-color); transform: scaleX(0); transform-origin: bottom right; transition: transform var(--timing-medium) var(--easing-smooth); z-index: -1; }
.navbar-link:hover::before, .navbar-link:focus::before { transform: scaleX(1); transform-origin: bottom left; }
.navbar-link:hover, .navbar-link:focus { background-color: var(--hover-bg); color: var(--text-secondary); text-decoration: none; outline: none; }
.navbar-link.active { color: var(--text-secondary); background-color: var(--active-bg); text-decoration: none; }
.navbar-link.active::before { transform: scaleX(1); background-color: var(--accent-color); }


/* --- Desktop User Menu --- */
.user-menu {
    /* ... existing styles ... */
    position: relative;
    display: flex;
    align-items: center;
    background-color: var(--bg-tertiary);
    padding: 0.4rem 0.8rem;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color var(--timing-medium) var(--easing-smooth);
    gap: 0.5rem;
    border: 1px solid transparent;
    font-size: 0.85rem;
    flex-shrink: 0;
}
.user-menu:hover,
.user-menu.focused {
    background-color: var(--bg-accent);
    border-color: var(--border-color);
}
.user-menu-value { font-weight: 600; color: var(--text-primary); }
.user-menu-arrow { color: var(--text-secondary); font-size: 1em; transition: transform var(--timing-medium) var(--easing-smooth); }
.user-menu.focused .user-menu-arrow { transform: rotate(180deg); }

/* --- Desktop User Dropdown Content --- */
.dropdown-content {
    /* ... existing styles ... */
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: calc(100% + 10px);
    right: 0;
    background-color: var(--dropdown-bg);
    backdrop-filter: blur(5px);
    min-width: 160px;
    box-shadow: 0px 10px 25px rgba(0,0,0,0.5);
    border-radius: 8px;
    padding: 0.8rem;
    z-index: 1001;
    border: 1px solid var(--border-color);
    transform-origin: top right;
    transform: translateY(5px) scale(0.98);
    transition: opacity var(--timing-medium) var(--easing-smooth),
                visibility 0s linear var(--timing-medium),
                transform var(--timing-medium) var(--easing-smooth);
    pointer-events: none;
    font-size: 0.85rem;
}
/* Remove mobile-specific class styling from desktop */
.dropdown-content.mobile-dropdown { display: none; }

.user-menu.focused .dropdown-content {
    opacity: 1;
    visibility: visible;
    transform: translateY(0) scale(1);
    pointer-events: auto;
    transition: opacity var(--timing-medium) var(--easing-smooth),
                visibility 0s linear 0s,
                transform var(--timing-medium) var(--easing-smooth);
}
.dropdown-content p { margin: 0 0 0.6rem; padding: 0 0.5rem; color: var(--text-primary); font-size: 0.9em; }
.dropdown-content hr { border: none; height: 1px; background-color: var(--border-color); margin: 0.6rem 0; }

/* --- Logout Link (Shared styles, adjusted for context) --- */
.logout-link {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    width: 100%;
    box-sizing: border-box;
    padding: 0.6rem 0.8rem;
    text-align: left;
    text-decoration: none;
    color: var(--accent-red);
    font-weight: 600;
    background: none;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color var(--timing-fast) var(--easing-smooth), color var(--timing-fast) var(--easing-smooth);
    font-size: inherit; /* Inherit font size from context */
}
.logout-link:hover {
    color: #fff;
    background-color: rgba(245, 54, 92, 0.8);
}

/* --- TOGGLE BUTTON --- */
.navbar-toggle {
    display: none; /* Hidden by default */
    /* ... existing styles ... */
    background: none;
    border: none;
    color: var(--text-secondary);
    font-size: 1.6rem;
    cursor: pointer;
    padding: 0.3rem;
    margin-left: 0.5rem;
    line-height: 1;
    order: 3;
    border-radius: 50%;
    width: 36px;
    height: 36px;
    place-items: center;
    transition: background-color var(--timing-fast) var(--easing-smooth), transform var(--timing-medium) var(--easing-smooth);
    z-index: 1001;
}
.navbar-toggle:hover { background-color: var(--hover-bg); }
.navbar-toggle:active { transform: scale(0.9); }


/* --- MOBILE STYLES --- */
@media (max-width: var(--mobile-breakpoint)) {
    .navbar {
        /* ... existing mobile navbar styles ... */
        padding: 0.6rem 1rem;
        width: calc(100% - 1.5rem);
        top: 0.75rem;
        border-radius: 8px;
    }

    .navbar-left { gap: 0; margin-right: auto; }
    .navbar-right { gap: 0.5rem; }

    .navbar-brand { margin-right: 0.5rem; font-size: 1.1rem; }
    .navbar-logo-img { height: 24px; }

    /* --- Hide Desktop User Menu --- */
    .user-menu.desktop-only {
        display: none;
    }

    /* --- Configure Mobile Links Container --- */
    .navbar-links {
        /* ... existing mobile position/transition styles ... */
        display: flex;
        flex-direction: column;
        position: absolute;
        top: calc(100% + 0.5rem);
        left: 0;
        right: 0;
        width: 100%;
        margin: 0 auto;
        background-color: var(--mobile-nav-bg);
        padding: 0.5rem 0;
        border-radius: 8px;
        box-shadow: 0 8px 20px rgba(0,0,0,0.4);
        border: 1px solid var(--border-color);
        border-top: none;
        z-index: 999;
        opacity: 0;
        transform: translateY(-10px);
        visibility: hidden;
        pointer-events: none;
        max-height: 0;
        overflow: hidden;
        transition: max-height var(--timing-medium) var(--easing-smooth),
                    opacity var(--timing-medium) var(--easing-smooth),
                    transform var(--timing-medium) var(--easing-smooth),
                    visibility 0s linear var(--timing-medium);
    }
    .navbar-links.mobile-active {
        /* ... existing mobile active styles ... */
        opacity: 1;
        transform: translateY(0);
        visibility: visible;
        pointer-events: auto;
        max-height: 80vh; /* Increased height slightly */
        transition: max-height var(--timing-medium) var(--easing-smooth),
                    opacity var(--timing-medium) var(--easing-smooth),
                    transform var(--timing-medium) var(--easing-smooth),
                    visibility 0s linear 0s;
    }

    /* --- Mobile Link Styling (Apply to regular links) --- */
    .navbar-link {
        /* ... existing mobile link styles ... */
        width: 100%;
        padding: 0.9rem 1.8rem;
        justify-content: flex-start;
        border-radius: 0;
        font-size: 0.95rem;
        border-bottom: 1px solid var(--border-color);
        overflow: hidden; /* Reset from desktop */
        gap: 0.8rem; /* Added gap for icon */
    }
     /* No bottom border on the container for mobile user menu items */
    .navbar-link:has(+ .navbar-link-mobile-usermenu) {
         border-bottom: none;
    }
    /* Disable desktop effects */
    .navbar-link::before { display: none; }
    .navbar-link:hover, .navbar-link:focus { background-color: var(--hover-bg); color: var(--text-secondary); }
    .navbar-link.active { background-color: var(--accent-color); color: var(--text-secondary); }

    /* --- Mobile User Menu Container Styling --- */
    .navbar-link-mobile-usermenu {
        display: block; /* Show this container on mobile */
        padding: 0.5rem 0; /* Add some padding around the items inside */
        border-top: 1px solid var(--border-color); /* Separator line */
        margin-top: 5px; /* Space above the separator */
    }

    /* --- Mobile Logout Button Styling (Looks like a nav link) --- */
    .mobile-logout-button {
        /* Inherit general link styles */
        display: flex;
        align-items: center;
        gap: 0.8rem; /* Match nav link gap */
        width: 100%;
        padding: 0.9rem 1.8rem; /* Match nav link padding */
        justify-content: flex-start;
        font-size: 0.95rem; /* Match nav link font size */
        background: none;
        border: none;
        cursor: pointer;
        text-align: left;
        /* Use logout color */
        color: var(--accent-red);
        font-weight: 500; /* Match nav link weight */
        transition: background-color var(--timing-fast) var(--easing-smooth), color var(--timing-fast) var(--easing-smooth);
    }
    .mobile-logout-button:hover {
         background-color: rgba(245, 54, 92, 0.15); /* Subtle red hover */
         color: #fff; /* Brighter red on hover */
    }
    .mobile-logout-button svg {
         font-size: 1.2em; /* Match icon size if needed */
    }


    /* --- Show Mobile Toggle Button --- */
    .navbar-toggle {
        display: grid;
    }
}

/* ... (keep existing smaller screen adjustments and reduced motion styles) ... */
@media (max-width: 400px) {
     /* .user-menu { padding: 0.3rem 0.6rem; font-size: 0.8rem; } */ /* No longer needed as it's hidden */
     .navbar-toggle { font-size: 1.5rem; width: 32px; height: 32px; }
     .navbar-brand { font-size: 1rem; gap: 0.4rem; }
     .navbar-logo-img { height: 22px; }
     /* Adjust mobile link padding slightly for very small screens */
     .navbar-link, .mobile-logout-button { padding: 0.8rem 1.5rem; font-size: 0.9rem; }
}

@media (prefers-reduced-motion: reduce) {
  .navbar,
  .navbar-link,
  .user-menu,
  .dropdown-content,
  .navbar-toggle,
  .download-list-item, /* Added Download items */
  .download-item-icon, /* Added Download items */
  .button-download {   /* Added Download items */
    animation: none !important;
    transition: none !important;
  }
  .navbar-link::before { transition: none !important; }
  .navbar-brand:hover { transform: none; }
  .user-menu.focused .user-menu-arrow { transform: none; }
  /* Add specific Download hover disables if needed */
   .download-list-item:hover { transform: none; }
   .download-list-item:hover .download-item-icon { transform: none; }
   .button-download:hover:not(:disabled) { transform: none; }
   .button-download:active:not(:disabled) { transform: none; } /* Added active state */
}

/* --- END OF FILE Navbar.css --- */