.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(32, 32, 32, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 20px;
    animation: fadeIn 0.25s cubic-bezier(0.25, 0.1, 0.25, 1) forwards;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

.modal-content {
    background-color: #202020;
    color: #ffffff;
    border-radius: 8px;
    padding: 0;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    animation: slideIn 0.25s cubic-bezier(0.25, 0.1, 0.25, 1) forwards;
}

@keyframes slideIn {
    from { transform: scale(0.9) translateY(20px); opacity: 0; }
    to { transform: scale(1) translateY(0); opacity: 1; }
}

.modal-header-section {
    padding: 24px 24px 16px 24px;
    text-align: left;
}

.modal-header-section h2 {
    font-size: 1.4em;
    font-weight: 600;
    margin: 0 0 10px 0;
    color: #ffffff;
}

.modal-header-section p {
    font-size: 1em;
    color: #cccccc;
    line-height: 1.5;
    margin: 0;
}

.modal-separator {
    border: none;
    height: 0.5px;
    background-color: #404040;
    margin: 0;
}

.modal-actions-section {
    padding: 16px 24px 24px 24px;
    background-color: #252525;
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    border-top: 1px solid #404040;
}

.modal-button {
    padding: 10px 20px;
    border-radius: 6px;
    font-size: 0.95em;
    font-weight: 600;
    cursor: pointer;
    transition: transform 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), box-shadow 0.15s cubic-bezier(0.25, 0.1, 0.25, 1), border-color 0.15s cubic-bezier(0.25, 0.1, 0.25, 1);
    border: 1px solid transparent;
    text-align: center;
    min-width: 90px;
}

.modal-button:hover {
    transform: scale(1.02);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.modal-button:active {
    transform: scale(0.98);
}

.modal-button.button-confirm {
    background-color: #0078D4;
    color: white;
    border-color: #0078D4;
}
.modal-button.button-confirm:hover {
    background-color: #005a9e;
    border-color: #005a9e;
}
.modal-button.button-confirm:active {
    background-color: #004c87;
    border-color: #004c87;
}

.modal-button.button-try {
    background-color: #252525;
    color: #ffffff;
    border: 1px solid #404040;
}
.modal-button.button-try:hover {
    background-color: #303030;
    border-color: #505050;
}
.modal-button.button-try:active {
    background-color: #353535;
    border-color: #606060;
}

.modal-button.button-cancel {
    background-color: #252525;
    color: #ffffff;
    border: 1px solid #404040;
}
.modal-button.button-cancel:hover {
    background-color: #303030;
    border-color: #505050;
}
.modal-button.button-cancel:active {
    background-color: #353535;
    border-color: #606060;
}

.modal-button.button-danger {
    background-color: #dc3545;
    color: white;
    border-color: #dc3545;
}
.modal-button.button-danger:hover {
    background-color: #c82333;
    border-color: #bd2130;
}
.modal-button.button-danger:active {
    background-color: #bd2130;
    border-color: #b21f2d;
}