/* START OF FILE Download.css */

:root {
    --download-list-item-bg: var(--bg-secondary);
    --download-list-item-border: var(--bg-accent);
    --download-icon-color: var(--text-secondary);
    --transition-duration: 0.25s;
    --transition-timing: ease-out;
}

@keyframes fadeInSlideUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.download-page-container {
    width: 100%;
    max-width: 950px;
    margin: 0 auto;
}

.download-page-title {
    color: var(--text-primary);
    margin-bottom: 30px;
    text-align: left;
    font-size: 1.8em;
    font-weight: 600;
    /* border-bottom: 1px solid var(--bg-accent); <-- REMOVED THIS LINE */
    padding-bottom: 10px; /* Kept padding for spacing */
}

.download-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
}

.download-list-item {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 18px 25px;
    background-color: var(--download-list-item-bg);
    border-radius: 15px;
    border: 1px solid var(--download-list-item-border);
    transition: background-color var(--transition-duration) var(--transition-timing),
                box-shadow var(--transition-duration) var(--transition-timing),
                transform var(--transition-duration) var(--transition-timing);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    opacity: 0; /* Start hidden for animation */
    animation: fadeInSlideUp 0.4s var(--transition-timing) forwards;
    animation-delay: 0.1s; /* Slight delay before animation starts */
    /* For staggered effect, you'd typically add varying animation-delay via JS */
}

.download-list-item:hover {
    background-color: var(--bg-tertiary);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transform: translateY(-3px);
}

.download-item-icon {
    flex-shrink: 0;
    color: var(--download-icon-color);
    font-size: 2.5em;
    transition: transform var(--transition-duration) var(--transition-timing);
}

.download-list-item:hover .download-item-icon {
    transform: scale(1.08);
}

.download-item-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 5px;
    overflow: hidden;
}

.download-item-title {
    font-size: 1.15em;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.download-item-description {
    font-size: 0.9em;
    color: var(--text-secondary);
    line-height: 1.5;
    margin: 0;
}

.download-item-meta {
    font-size: 0.8em;
    color: var(--text-disabled);
    display: flex;
    gap: 15px;
    margin-top: 5px;
}

.download-item-meta span {
    white-space: nowrap;
}

.download-item-meta strong {
    color: var(--text-secondary);
    font-weight: 500;
}

.download-item-button-wrapper {
    flex-shrink: 0;
    margin-left: auto;
    padding-left: 15px;
}

.button-download {
    background: linear-gradient(to right, #409dee, #00a2ff);
    color: white;
    box-shadow: 0 2px 5px rgba(137, 101, 224, 0.25);
    width: auto;
    min-width: 120px;
    padding: 8px 18px;
    text-decoration: none;
    border: none; /* Assuming base button doesn't need border */
    cursor: pointer;
    display: inline-flex; /* Good practice for alignment */
    align-items: center;
    justify-content: center;
    border-radius: 8px; /* Assuming from base */
    font-weight: 500; /* Assuming from base */
    transition: background var(--transition-duration) var(--transition-timing),
                box-shadow var(--transition-duration) var(--transition-timing),
                transform var(--transition-duration) var(--transition-timing),
                color var(--transition-duration) var(--transition-timing);
}

.button-download:hover:not(:disabled) {
    background: linear-gradient(to right, #2a7ac0, #00a2ff);
    text-decoration: none;
    color: white;
    transform: translateY(-2px);
}

.button-download:disabled {
     background: var(--bg-tertiary);
     border-color: var(--bg-accent);
     color: var(--text-disabled);
     box-shadow: none;
     cursor: not-allowed;
     transform: none; /* Ensure no transform on disabled hover */
}

/* --- MOBILE STYLES --- */
@media (max-width: 768px) {
    .download-page-container {
        max-width: 100%;
        padding: 0 15px;
    }

    .download-page-title {
        font-size: 1.6em;
        margin-bottom: 20px;
        /* No border needed here either */
    }

    .download-list-item {
        flex-direction: column;
        align-items: flex-start;
        padding: 15px;
        gap: 15px;
    }
    /* Disable hover transform on mobile/stacked view */
    .download-list-item:hover {
         transform: none; /* <-- UNCOMMENTED/MODIFIED THIS */
    }

    .download-item-icon {
        font-size: 2em;
    }

    .download-item-content {
        width: 100%;
    }

    .download-item-title {
       white-space: normal; /* Allow title wrapping */
    }

    .download-item-meta {
       flex-direction: column; /* Stack meta items */
       gap: 5px;
       margin-top: 10px;
    }

    .download-item-button-wrapper {
        width: 100%;
        margin-left: 0;
        padding-left: 0;
        margin-top: 10px;
    }

    .button-download {
        width: 100%; /* Make button full width */
        justify-content: center;
        padding: 10px 15px;
    }
    /* Disable button hover transform on mobile */
    .button-download:hover:not(:disabled) {
        transform: none; /* <-- UNCOMMENTED THIS */
    }
}

/* Accessibility: Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .download-list-item,
  .download-item-icon,
  .button-download {
    animation: none !important;
    transition: none !important;
  }

  .download-list-item:hover {
     transform: none;
  }

  .download-list-item:hover .download-item-icon {
      transform: none;
  }

  .button-download:hover:not(:disabled) {
     transform: none;
  }
}
/* END OF FILE Download.css */