/* START OF FILE NotificationBanner.css */
:root {
    --accent-green: #28a745;
    --accent-red: #dc3545;
    --accent-blue: #363636;
    --notification-dark-bg: #22272B;
    --notification-light-text: #E0E0E0;
    --notification-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px; /* Softer shadow */
    --backdrop-bg: rgba(0, 0, 0, 0.15);
    --initial-size: 48px; /* Slightly adjusted */
    --top-padding: 16px;
    --border-radius-circle: 50%;
    --border-radius-pill: 24px; /* Matched to half of initial-size approx */
    --max-banner-width: min(500px, calc(100vw - 32px));
}

* { box-sizing: border-box; }

.notification-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    z-index: 2000;
    pointer-events: none;
    padding-top: var(--top-padding);
}

.notification-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--backdrop-bg);
    backdrop-filter: blur(2px); /* Slightly more blur */
    pointer-events: none;
    z-index: -1;
}

.notification-banner {
    display: flex;
    align-items: center;
    position: relative;
    color: var(--notification-light-text);
    font-family: 'Roboto', sans-serif;
    font-size: 0.9em;
    line-height: 1.3;
    box-shadow: var(--notification-shadow);
    background-color: var(--notification-dark-bg);
    pointer-events: all;
    overflow: hidden;
    cursor: default;
    max-width: var(--max-banner-width);
}

.notification-icon {
    margin-right: 10px;
    font-size: 1.4em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
}

.notification-message {
    text-align: left;
    display: inline-block; /* Ensures transform applies correctly */
    vertical-align: middle;
    line-height: 1.3;
    overflow: hidden;
    white-space: nowrap;
}

.notification-banner.notification-success { background-color: var(--accent-green); }
.notification-banner.notification-error { background-color: var(--accent-red); }
.notification-banner.notification-info { background-color: var(--accent-blue); }