
:root {

    --warning-bg: #FEF9C3;
    --warning-border: #ffeeba;
    --warning-text: #523d00;
    --warning-icon-color: #ffc107;

    --note-bg: #e7f3fe; 
    --note-border: #b8d9fc;
    --note-text: #0a58ca;
    --note-icon-color: var(#4ab7ff, #9c5ee4);
}

.note-box {
    background-color: var(--note-bg);
    color: var(--note-text);
    border: 1px solid var(--note-border);
    border-radius: 10px; 
    padding: 12px 18px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    font-size: 0.95em;
    width: 100%;
    box-sizing: border-box;
    text-align: left;
}

.note-icon {
    color: var(--note-icon-color);
    margin-right: 10px;
    font-size: 1.3em;
    flex-shrink: 0;
}


.guides-page-container {
    padding-top: 80px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    margin: 0 auto;
}

.installation-guide-title {
    font-size: 2em;
    font-weight: 500;
    color: var(--text-primary);
    margin-bottom: 40px;
    text-align: center;
}

.guide-step {
    width: 100%;
    margin-bottom: 0.5rem;
}

.step-heading {
    font-size: 1.6em;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 0.75rem;
    text-align: left;
}

.step-description {
    font-size: 1em;
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 1.2rem;
    text-align: left;
}

.step-description a {
    color: var(--accent-blue);
    text-decoration: none;
    font-weight: 500;
}
.step-description a:hover {
    text-decoration: underline;
}

.warning-box {
    background-color: var(--warning-bg);
    color: var(--warning-text);
    border: 1px solid var(--warning-border);
    border-radius: 10px;
    padding: 12px 18px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    font-size: 0.95em;
    width: 100%;
    box-sizing: border-box;
    text-align: left;
}

.warning-icon {
    color: var(--warning-icon-color);
    margin-right: 10px;
    font-size: 1.3em;
    flex-shrink: 0;
}

.step-list {
    list-style: none;
    padding-left: 5px;
    margin-top: 0.5rem;
    text-align: left;
}

.step-list-item {
    color: var(--text-secondary);
    font-size: 1em;
    margin-bottom: 0.5rem;
    position: relative;
    padding-left: 20px;
}

.step-list-item::before {
    content: '•';
    color: var(--text-primary);
    font-weight: bold;
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    font-size: 1.2em;
}

@media (max-width: 768px) {
     /* Add note-box adjustments if needed for mobile */
    .note-box,
    .warning-box {
        font-size: 0.9em;
        padding: 10px 15px;
    }

    .guides-page-container {
        max-width: 90%;
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 70px;
    }

    .installation-guide-title {
        font-size: 1.7em;
        margin-bottom: 30px;
    }

    .step-heading {
        font-size: 1.4em;
    }

    .step-description,
    .step-list-item {
        font-size: 0.95em;
    }
}