:root {
  /* UPDATED PALETTE TO MATCH IMAGE (Using values from previous attempts) */
  --bg-primary: #020817;
  --bg-secondary: rgba(16, 22, 29, 0.624);
  --bg-tertiary: #2a2d4e;
  --bg-accent: #3a3d6a;
  --bg-welcome: #483d8b;

  --text-primary: #e8eaf6;
  --text-secondary: #a8acc3;
  --text-disabled: #7a7d8d;

  --accent-blue: #5e72e4;
  --maintance: #423e00;
  --accent-green: #0f5f3cad;
  --accent-red: #f5365c;
  --accent-yellow: #fb912f; 
  --accent-purple: #8965e0; 
  --accent-btn-gd: linear-gradient(to right, #3089d6, #CD0EF3);

  --shadow-color: rgba(0, 0, 0, 0.4);
  --card-shadow: 0 4px 10px var(--shadow-color);
  --card-hover-shadow: 0 7px 15px var(--shadow-color);

  /* Gradients for top borders */
  --gradient-border-details: linear-gradient(to right, #9cecfb, #65c7f7, #0052d4);
  --gradient-border-status: linear-gradient(to right, #fff33b, #f3903f, #e93e3a);
  --gradient-border-settings: linear-gradient(to right, #4EA2EA, #CD0EF3, #FB6D64);

  /* Define border thickness */
  --card-border-top-thickness: 5px;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: var(--bg-primary);
    color: var(--text-primary);
    line-height: 1.6;
}

a {
    color: var(--accent-blue);
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

button {
    font-family: inherit;
}

.dashboard-modern-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  padding-top: 80px; /* Adjust based on actual navbar height */
  min-height: 100vh;
  box-sizing: border-box;
}

.loading-message {
    min-height: 200px;
    /* Add specific styling if needed */
}

.error-message {
    max-width: 1100px;
    width: 90%;
    /* Add specific styling if needed */
}

.dashboard-error {
     background-color: rgba(245, 54, 92, 0.15);
     color: var(--accent-red);
     border: 1px solid rgba(245, 54, 92, 0.4);
     padding: 10px 15px;
     border-radius: 6px;
     margin-bottom: 20px;
}

.dashboard-error-main {
     background-color: rgba(251, 145, 47, 0.15);
     color: var(--accent-yellow);
     border: 1px solid rgba(251, 145, 47, 0.4);
     padding: 10px 15px;
     border-radius: 6px;
     margin-bottom: 25px;
     max-width: 1100px;
     width: 90%;
}

.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Keep 3 columns */
  gap: 25px;
  max-width: 1100px;
  width: 100%;
  margin-top: 20px;
}

/* --- Card Styling with TOP-ONLY Gradient Border --- */
.dashboard-card {
    background-color: var(--bg-secondary);
    border-radius: 8px; /* Keep rounded corners */
    box-shadow: var(--card-shadow);
    display: flex;
    flex-direction: column;
    position: relative; /* Needed for pseudo-element */
    overflow: hidden; /* Crucial to clip the pseudo-element */
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

    /* Subtle border for sides/bottom - adjust color/width if needed */
    border: 1px solid var(--bg-accent);
    border-top: none; /* Remove top border to make space for pseudo-element */
}

.dashboard-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: var(--card-border-top-thickness); /* Use variable for thickness */
    z-index: 1; /* Place above the main border but below content if needed */
    /* Gradient background applied per card type below */
    /* No border-radius needed here, parent overflow:hidden handles clipping */
}

/* Apply specific gradients to each card's pseudo-element */
.details-card::before {
    background: var(--gradient-border-details);
}

.status-card::before {
    background: var(--gradient-border-status);
}

/* Renaming settings-card to device-auth-card if the content changed */
.device-auth-card::before,
.settings-card::before { /* Keep settings-card for compatibility if needed */
    background: var(--gradient-border-settings);
}

.dashboard-card:hover {
    transform: translateY(-3px);
    box-shadow: var(--card-hover-shadow);
}

/* Card Header */
.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    /* Header sits below the gradient border, no special radius needed */
    background: linear-gradient(to right, rgba(42, 45, 78, 0.5), rgba(42, 45, 78, 0.1));
    border-bottom: 1px solid var(--bg-accent); /* Optional separator line */
}

.card-header h3 {
    margin: 0;
    font-size: 1.1em;
    font-weight: 600;
    color: var(--text-primary);
}

.card-header-icon {
    font-size: 1.3em; /* Adjust as needed */
    color: var(--text-secondary); /* Adjust as needed */
}
/* Make sure SVGs in headers match the target style */
.card-header svg {
    width: 20px; /* Example size */
    height: 20px; /* Example size */
    /* Add fill/stroke if needed, though gradients are complex */
}


/* Card Body */
.card-body {
    padding: 20px;
    flex-grow: 1;
}

/* Card Footer */
.card-footer {
    padding: 15px 20px;
    border-top: 1px solid var(--bg-accent); /* Optional separator */
    background-color: rgba(0, 0, 0, 0.15); /* Slightly darker footer */
    display: flex;
    justify-content: flex-end; /* Default */
    align-items: center;
    gap: 10px;
}

/* Info Rows - Match target styling */
.info-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    font-size: 0.9em; /* Slightly adjust if needed */
    border-bottom: 1px solid var(--bg-tertiary);
}
.info-row:last-child {
    border-bottom: none;
}

.info-label {
    font-weight: 500; /* Or slightly lighter if needed */
    color: var(--text-secondary);
    margin-right: 15px;
    white-space: nowrap;
}

.info-value {
    color: var(--text-primary);
    text-align: right;
    word-break: break-word; 
}

.hwid-value {
    font-family: 'Consolas', 'Courier New', Courier, monospace;
    font-size: 0.8em;
    color: #9fa7ff;
    background-color: rgba(94, 114, 228, 0.1); 
    padding: 3px 8px;
    border-radius: 4px;
    border: 1px solid rgba(94, 114, 228, 0.3); 
    user-select: all;
    display: inline-block; 
}

.button-reset-hwid {

  background-color:#3089d6;
  color: white;              
  border: none;               
  border-radius: 6px; 
  box-shadow: 0 2px 5px rgba(137, 101, 224, 0.3); 
  width: 100%;  
  padding: 10px 20px;
  display: inline-flex;  
  align-items: center;                
  justify-content: center;                
  gap: 8px;
  margin-bottom: 15px;
  font-size: 0.95em;               
  font-weight: 600;                   
  font-family: inherit;                
  text-decoration: none;            
  white-space: nowrap;            
  line-height: 1.2;   

  cursor: pointer; 
  transition: background-color 0.2s ease, box-shadow 0.2s ease, transform 0.1s ease; 
}


.button-reset-hwid:hover:not(:disabled) {
  background-color: #7a58d0;
  box-shadow: 0 4px 8px rgba(137, 101, 224, 0.4); 
}


.button-reset-hwid:active:not(:disabled) {
  transform: translateY(1px);
  box-shadow: 0 1px 3px rgba(137, 101, 224, 0.3);
}

.button-reset-hwid:disabled {
  background-color: var(--bg-tertiary);
  color: var(--text-disabled);
  border: 1px solid var(--bg-accent);
  box-shadow: none;
  cursor: not-allowed;
  opacity: 0.6;
  transform: none;
}

.button-reset-hwid svg {
   width: 1em;
   height: 1em; 
}


.status-badge {
  padding: 4px 10px; 
  border-radius: 4px;
  font-size: 0.75em;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-family: 'Consolas', Courier, monospace;
  color: #fff; 
  display: inline-flex;
  align-items: center;
  gap: 5px;
  text-align: center;
  text-transform: capitalize;
}
/* Active/Safe Badge */
.badge-active, 
.status-safe {
    background-color: var(--accent-green);
    /* Add checkmark icon styling if using inline SVG or font icon */
}
.badge-active svg { /* Example if you add an SVG */
    margin-right: 4px;
    width: 12px;
    height: 12px;
}

/* Other Badges */
.badge-banned { background-color: var(--accent-red); }
.badge-inactive { background-color: var(--text-disabled); color: var(--text-primary); }
.status-risk { background-color: var(--accent-yellow); }
.status-maintenance { background-color: var(--maintance); }
.status-loading { background-color: var(--bg-tertiary); color: var(--text-secondary); }
.status-error { background-color: var(--accent-red); }
.status-unknown { background-color: var(--text-disabled); color: var(--text-primary); }

/* Device Authorization / Settings Card Specifics */
.device-auth-description {
    font-size: 0.85em; /* Adjust size */
    color: var(--text-secondary);
    margin-bottom: 20px;
    line-height: 1.5;
}

/* Renamed from reset-info to match content */
.reset-usage-info,
.reset-info { /* Keep reset-info for backward compat if needed */
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    font-size: 0.95em;
    color: var(--text-primary);
}
.reset-usage-info .info-label,
.reset-info .info-label {
     color: var(--text-secondary); /* Label secondary */
}
.reset-usage-info .info-value,
.reset-info .info-value {
    font-weight: 600; /* Make value slightly bolder */
}


.progress-bar-container {
    width: 100%;
    background-color: var(--bg-tertiary);
    border-radius: 5px;
    height: 8px; /* Keep thin bar */
    overflow: hidden;
    margin-bottom: 15px;
}

.progress-bar {
    height: 100%;
    background-color: var(--accent-red); /* Red color from image */
    border-radius: 5px;
    transition: width 0.5s cubic-bezier(0.25, 0.1, 0.25, 1);
}

.device-warning-box {
    margin-top: 15px;
    margin-bottom: 15px;
    font-size: 0.9em;
    color: var(--accent-red); /* Red text */
    text-align: left;
    display: flex;
    align-items: center;
    gap: 8px;
    background-color: rgba(245, 54, 92, 0.1); /* Subtle red background */
    padding: 10px 15px;
    border-radius: 4px;
    border: 1px solid rgba(245, 54, 92, 0.3); /* Subtle red border */
}
.device-warning-box svg {
    font-size: 1.3em; /* Adjust icon size */
    flex-shrink: 0;
}

.max-reset-message {
    margin-top: 15px;
    font-size: 0.9em;
    color: var(--accent-red);
    text-align: left;
    padding: 8px 0;
    /* Remove flex if icon is not needed or handled differently */
    /* display: flex; */
    /* align-items: center; */
    /* gap: 5px; */
}

/* --- Buttons --- */
.button-base {
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 0.95em;
    font-weight: 600;
    transition: all 0.2s ease;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    text-decoration: none;
    white-space: nowrap;
    line-height: 1.2;
}
.button-base:active:not(:disabled) {
    transform: translateY(1px);
}

/* Reset Device ID Button (Purple) */
.button-primary {
    background-color: #4EA2EA;
    color: white;
    box-shadow: 0 2px 5px rgba(101, 154, 224, 0.3);
    width: 100%; /* Full width in target image */
}
.button-primary:hover:not(:disabled) {
    background-color: #0077ff;
    box-shadow: 0 4px 8px rgba(137, 101, 224, 0.4);
}

/* Experiment Active Button (Yellow/Orange) */
.button-yellow {
    background-color: var(--accent-yellow);
    color: #fff;
    box-shadow: 0 2px 5px rgba(251, 145, 47, 0.3);
    width: 100%; /* Full width in target image */
    justify-content: center;
}
.button-yellow:hover:not(:disabled) {
    background-color: #f97e1a;
    box-shadow: 0 4px 8px rgba(251, 145, 47, 0.4);
}

.button-secondary {
  background-color: #ffffff1f;
  border-radius: 5px;
  border: 1px solid #ffffff15;
  padding: 5px 5px;
}

/* Specific button widths if needed */
.button-reset-device { /* Match class used in JS if different */
    width: 100%;
}
.button-experiment-active {
    width: 100%;
}


.button-base:disabled,
button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    box-shadow: none;
    transform: none;
    background-color: var(--bg-tertiary);
    color: var(--text-disabled);
    border: 1px solid var(--bg-accent);
}
.button-primary:disabled {
     background-color: var(--bg-tertiary);
     border-color: var(--bg-accent);
     color: var(--text-disabled);
     box-shadow: none;
}
.button-yellow:disabled {
     background-color: var(--bg-tertiary);
     border-color: var(--bg-accent);
     color: var(--text-disabled);
     box-shadow: none;
}



@media (max-width: 992px) {
    .dashboard-grid {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
}

@media (max-width: 768px) {
    .dashboard-grid {
        grid-template-columns: 1fr;
        gap: 20px;
    }
    /* Keep mobile adjustments for info rows if still relevant */
    .info-row, .reset-usage-info, .reset-info {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px 0;
    }
    .info-label, .reset-usage-info .info-label, .reset-info .info-label {
        margin-bottom: 5px;
        font-weight: 600;
        color: var(--text-secondary);
    }
    .info-value, .reset-usage-info .info-value, .reset-info .info-value {
        text-align: left;
        width: 100%;
        color: var(--text-primary);
    }
    .hwid-value {
        max-width: 100%;
        overflow-wrap: break-word;
    }
    /* Add other mobile adjustments as needed */
}